import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);//vue

//Vuex.Store 
const store = new Vuex.Store({
    state:{//
        "openId":"",
        "age":18,
		"nickName":'',
		"avatar":''
    },
	mutations:{//
		info(state,obj){
			state.nickName = obj.nickName
			state.avatar = obj.avatar
		}
	}
})
export default store